.product-card-labels {
	@media (max-width: 62em) {
		display: flex;
		flex-wrap: wrap;
		gap: 0.5rem;
	}
	&__item {
		position: absolute;
		@media (max-width: 62em) {
			position: static;
		}
		&:nth-child(1) {
			top: 20px;
			right: 40px;
			// @media (max-width: 48em) {
			// 	top: 5px;
			// 	right: 5px;
			// }
		}
		&:nth-child(2) {
			top: -35px;
			right: 125px;
			// @media (max-width: 48em) {
			// 	top: -30px;
			// 	right: 85px;
			// }
		}
		&:nth-child(3) {
			top: 110px;
			right: -10px;
			// @media (max-width: 48em) {
			// 	top: 90px;
			// 	right: -15px;
			// }
		}
		&:nth-child(4) {
			top: -50px;
			right: 225px;
			// @media (max-width: 48em) {
			// 	top: -15px;
			// 	right: 170px;
			// }
		}
	}
}
