@import "../../assets/styles/variables.scss";
.product-card-label {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100px;
	height: 100px;
	padding: 8px;
	border-radius: 50%;
	border: 8px solid #ffffff;
	background-color: $accentColor;
	font-size: 1.25rem;
	line-height: 120%;
	text-align: center;
	letter-spacing: 0.04em;
	color: $mainDarkColor;
	@media (max-width: 62em) {
		font-size: 1rem;
		width: 75px;
		height: 75px;
		border: none;
	}
	&--hit {
		font-size: 2rem;
		color: $priceColor;
		@media (max-width: 48em) {
			font-size: 1.5rem;
		}
	}
	&--new-taste {
		background-color: $blue2Color;
		color: $blue1Color;
	}
	&--soon {
	}
	&--discount {
		background-color: $accentHoverColor;
	}
}
